import React, { Component } from "react";
import { Link } from "react-router-dom";
import Input from "./Input";
//import Header from "./Header";

class NewSettings extends Component {
    constructor(props) {
        super(props);
        this.state = {
            inputValue1: "15",
            inputValue2: "5",
            inputValue3: "30",
            inputValue4: "10",
            inputValue5: "200",
        };

        this.handleValueChange = this.handleValueChange.bind(this);
        this.onSubmitForm = this.onSubmitForm.bind(this);
    }

    handleValueChange(e) {
        console.log(e);
    }

    onSubmitForm() {
        console.log(this.state);
    }

    render() {
        const items = [];

        for (let i = 0; i < 5; i++) {
            let className = `input${i + 1}`;
            let name = `inputValue${i + 1}`;

            items.push(
                <Input
                    key={i}
                    inputClassName={className}
                    name={name}
                    value={this.state[name]}
                    onChange={(val) => {
                        this.setState({ [name]: val });
                    }}
                />
            );
        }

        return (
            <div className="new-settings">
                {/* <Header /> */}
                <div className="section">
                    <div className="section-title">
                        <Link to="/">
                            <img
                                src="/assets/left-arrow.png"
                                alt="logo"
                                width="80"
                            />
                        </Link>
                        Enter process settings
                    </div>
                </div>
                <div className="steps">
                    <div className="specimen">Specimen</div>
                    <div className="step-1">Step 1</div>
                    <div className="step-2">Step 2</div>
                    <div className="step-3">Step 3</div>
                    <div className="step-4">Step 4</div>
                </div>
                <div className="select-specimen">
                    <div className="select-specimen-type">
                        <p>Select specimen type</p>
                        <select>
                            <option>Axisymmetrical specimen</option>
                            <option disabled>Flat specimen</option>
                        </select>
                    </div>
                    <div className="select-specimen-profile">
                        <p>Select specimen profile</p>
                        <select>
                            <option>
                                Tangentially blending fillets between the ends
                            </option>
                            <option>Continuous radius</option>
                        </select>
                    </div>
                </div>
                <div className="set-dimensions">
                    <img
                        src="/assets/AxisymmetricalSpecimenWithTangentiallyBlendingFillets.png"
                        alt="logo"
                        width="1700"
                    />
                    {items}
                </div>
            </div>
        );
    }
}

export default NewSettings;



